
import {Vue, Component, Prop} from 'vue-property-decorator';

@Component({})
export default class backHeader extends Vue {
  @Prop({ default: "" }) private title!: string;
  @Prop({ default: "" }) private back_url!: string;
  private FnBack() {
    if (this.back_url) {
      this.$router.push(this.back_url);
    } else {
      this.$router.go(-1);
    }
  }

}
